import { EventEmitter, Injectable } from '@angular/core';

import { assign } from 'lodash-es';
import { pick } from 'lodash-es';

import { CODE_EDITOR_EVENTS } from './events';

@Injectable()
export class CodeEditorEventService {
    public eventNames = Object.keys(CODE_EDITOR_EVENTS);
    private _events: any = {}; // Ã¤Â½Â¿Ã§ÂÂ¨Ã¤ÂºÂÃ¤Â»Â¶

    /**
     * Ã¨Â®Â¾Ã§Â½Â®Ã¥ÂÂ¯Ã§ÂÂ¨Ã¤ÂºÂÃ¤Â»Â¶
     * @param  Ã¤ÂºÂÃ¤Â»Â¶
     */
    addEvent(event){
        assign(this._events, event);
        // console.log(this.events);
    }

    fireEvent(event) {
        this._events[event.eventName].emit(event);
        // console.log("Emit Event:" + event);
    }
}
