import { Component, OnInit, ViewChild, Input, OnChanges,Injector, Output,Optional, EventEmitter } from '@angular/core';
import { FunctionEntity,FunctionEntitys } from '../funcentity';
import { DatagridComponent } from '@farris/ui-datagrid';
import { ExpressionEventService } from '../expression-event-service';
import { FuncOnEntity } from './funcentitys';
import { HttpClient } from "@angular/common/http";
import { FunctionI18nResources } from '../i18n/FunctionI18nResource';
import { ExpressionEditorLocalePipe } from '../expression-editor-locale.pipe';

@Component({
  selector: 'app-expr-func-grid',
  templateUrl: './expr-func-grid.component.html',
  styleUrls: ['./expr-func-grid.component.css']
})
export class ExprFuncGridComponent implements OnInit,OnChanges {

  //Ã¥Â®ÂÃ¤Â¹Âi18nÃ¦Â ÂÃ¨Â¯Â
  private localeId : string;
  private localePipe :  ExpressionEditorLocalePipe;

  constructor(private eventService: ExpressionEventService,private injector: Injector,public http : HttpClient){
    // if(this.injector)
    // {
      // this.http=this.injector.get(HttpClient);
      //Ã¥ÂÂÃ¥Â§ÂÃ¥ÂÂi18nÃ¦Â ÂÃ¨Â¯Â
      this.localeId =  localStorage.getItem('languageCode');
      this.localePipe = new ExpressionEditorLocalePipe(this.localeId);
    // }
  }
  @ViewChild('dg') dg:DatagridComponent;

  @Input() currentFuncTabType:string="String";
  @Input() funcExtend: FunctionEntity[]=[];
  @Output() selectFuncRow = new EventEmitter();
  @Output() dbclickInsert = new EventEmitter();
  items:FunctionEntity[]=[];

  standardFunctions = [];     //Ã¨Â§ÂÃ¨ÂÂÃ¥ÂÂ½Ã¦ÂÂ°
  allFunctions = [];          //Ã¦ÂÂÃ¦ÂÂÃ¥ÂÂ½Ã¦ÂÂ°Ã¯Â¼ÂÃ¥ÂÂÃ¥ÂÂ«Ã¤Â¸ÂÃ¨Â§ÂÃ¨ÂÂ
  deprecatedFunctions = [
    "StringSubstring", "StringLength", "StringReplace", "StringToUpper", "StringToLower","StringIndexOf", "StringLastIndexOf", "StringTrimStart", "StringTrimEnd", "StringTrim","StringAdd",
    "Round", "BankerRound",
    "getComputeJsonData",
    "ToDecimal", "ToSingle"
  ];                          //Ã¥ÂÂ½Ã¦ÂÂ°Ã¨Â°ÂÃ¦ÂÂ´Ã¯Â¼ÂÃ¤Â¸ÂÃ¤ÂºÂÃ¥ÂÂ½Ã¦ÂÂ°Ã§ÂÂ±Ã¤ÂºÂÃ¥ÂÂ½Ã¥ÂÂÃ¤Â¸ÂÃ¨Â§ÂÃ¨ÂÂÃ¨ÂÂÃ¤Â¸ÂÃ¤Â½Â¿Ã§ÂÂ¨Ã¯Â¼ÂÃ¥Â¹Â¶Ã¤Â¸ÂºÃ¨Â¿ÂÃ¤ÂºÂÃ¥ÂÂ½Ã¦ÂÂ°Ã¦ÂÂÃ¤Â¾ÂÃ¨Â§ÂÃ¨ÂÂÃ¥ÂÂ½Ã¥ÂÂÃ§ÂÂÃ¥Â®ÂÃ§ÂÂ°Ã£ÂÂÃ¤Â¸ÂºÃ¤ÂºÂÃ¦ÂÂ¯Ã¦ÂÂÃ¥Â·Â²Ã§Â»ÂÃ¤Â¿ÂÃ¥Â­ÂÃ§ÂÂÃ¨Â¡Â¨Ã¨Â¾Â¾Ã¥Â¼ÂÃ¯Â¼ÂÃ¤Â¸ÂÃ¨Â§ÂÃ¨ÂÂÃ¥ÂÂ½Ã¥ÂÂÃ¥ÂÂ½Ã¦ÂÂ°Ã¥Â°ÂÃ¤Â¼ÂÃ¤Â¿ÂÃ§ÂÂÃ¯Â¼ÂÃ¤Â½ÂÃ¦ÂÂ¯Ã¤Â¼ÂÃ¨Â¢Â«Ã¨Â¿ÂÃ¦Â»Â¤Ã¦ÂÂ
  columns = [];
  ngOnInit() {
    this.eventService.subClearFuncSelect().subscribe(()=>{
      this.dg.clearAll();
    })
    this.columns = [
      { field: 'name', title: this.localePipe.transform("FunctionSemanticName"),width:180},
      { field: 'key', title: this.localePipe.transform("FunctionName"),width:180},
      { field: 'param', title: this.localePipe.transform("FunctionParameter"),width:250}
    ];
    //Ã¨ÂÂ·Ã¥ÂÂi18nÃ¥ÂÂ½Ã¦ÂÂ°Ã¨ÂµÂÃ¦ÂºÂÃ¯Â¼ÂÃ¨Â¿ÂÃ¦Â»Â¤Ã¤Â¸ÂÃ¨Â§ÂÃ¨ÂÂÃ¥ÂÂ½Ã¥ÂÂÃ¥ÂÂ½Ã¦ÂÂ°
    this.allFunctions = FunctionI18nResources[this.localeId] || FunctionI18nResources["zh-CHS"];
    for(let i=0;i<this.allFunctions.length;i++){
      if(!this.deprecatedFunctions.includes(this.allFunctions[i].key)){
        this.standardFunctions.push(this.allFunctions[i]);
      }
    }

      switch(this.currentFuncTabType)
      {
        case "String":
          this.findFunctionByType("String");break;
        case "DateTime":
          this.findFunctionByType("DateTime");break;
        case "Aggregate":
          this.findFunctionByType("Aggregate");break;
        case "Translate":
          this.findFunctionByType("Translate");break;
        case "Estimate":
          this.findFunctionByType("Estimate");break;
        case "Math":
          this.findFunctionByType("Math");break;
        case "Analytical":
          this.findFunctionByType("Analytical");break;
        case "Advanced":
          this.findFunctionByType("Advanced");break;
        case "Extend":
          this.findFunctionByType("Extend");break;
      }
      let itmeList = [];
      /**
       * Ã¥Â°ÂÃ¨Â£ÂÃ¦ÂÂÃ¤Â¾ÂÃ§Â»ÂÃ¨Â¡Â¨Ã¨Â¾Â¾Ã¥Â¼ÂÃ§Â¼ÂÃ¨Â¾ÂÃ¥ÂÂ¨
       * 1Ã¯Â¼ÂAdvancedÃ¤Â½Â¿Ã§ÂÂ¨key,Ã¤Â¸ÂÃ¤Â½Â¿Ã§ÂÂ¨name
       */
      this.items.forEach(funcitem => {
        let func = new FuncOnEntity;
        if(funcitem.type=="Advanced"){
          func.name = funcitem.key;
        }else{
          func.name = funcitem.name;
        }
        func.key = funcitem.key;
        func.params = funcitem.params;
        func.type = funcitem.type;
        func.className = funcitem.className;
        func.description = funcitem.description;
        func.param = "(";      
        for (let i = 0; i < funcitem.params.length - 1; i++) {
          // func.param = func.param + funcitem.params[i].name + ":" + funcitem.params[i].type + ",";
          func.param = func.param + funcitem.params[i].key + ",";
        }
        if(funcitem.params.length!=0)
        {
          // func.param = func.param + funcitem.params[funcitem.params.length - 1].name + ":" + funcitem.params[funcitem.params.length - 1].type + ")";
          func.param = func.param + funcitem.params[funcitem.params.length - 1].key  + ")";
        }
        else func.param = func.param + ")";
        itmeList.push(func);
      });
      this.items = itmeList;
    }
  ngOnChanges(): void {
  }
  ondblclickRow($event)
  {
    this.dbclickInsert.emit($event.rowData);
  }
  selectRowChange($event)
  {
    this.selectFuncRow.emit($event.data);
  }
  findFunctionByType(type:string){
    this.items=[];
    this.standardFunctions.forEach(element=>{
      if(element.type==type){
        this.items.push(element);
      };
    });
  }
}
